.contact__title {
  position: relative;
  display: inline-block;
  margin: 0;
  font-family: inherit;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  vertical-align: baseline;
  color: @black;
  padding-right: 21px;
}

.contact__list {
  padding-top: 35px;
  padding-bottom: 20px;
}

.contact__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contact__item:not(:last-child) {
  margin-bottom: 9px;
}

.contact__label {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @text-grey;
  margin-right: 9px;
  white-space: nowrap;
}

.contact__input {
  box-sizing: border-box;
  padding: 14px 16px;
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @text-grey;
  border: 1px solid @grey-border;
  width: 100%;

  &:hover {
    border: 1px solid @special-grey;
  }

  &:focus {
    outline: 2px solid @text-grey;
  }

  &:invalid {
    outline: 2px solid @error;
  }
}

.contact__input--email {
  background-image: url("../img/phone/mail.svg");
  background-size: 18px 13px;
  background-repeat: no-repeat;
  background-position: right 15px center;

  &:focus {
    background-image: url("../img/phone/mail-focus.svg");
  }

  &:invalid {
    background-image: url("../img/phone/mail-error.svg")
  }

}



.contact__input--phone {
  background-image: url("../img/phone/phone.svg");
  background-repeat: no-repeat;
  background-position: right 13px center;
}

@media (min-width: @tablet-width) {
  .contact__title {
    font-size: 36px;
    line-height: 36px;
    padding-right: 49px;
  }

  .contact__wrapper {
    position: relative;
  }

  .contact__wrapper::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    background-color: @basic-green;
    right: 100%;
    margin-right: 40px;
  }

  .contact__list {
    padding-top: 70px;
    padding-bottom: 0;
  }

  .contact__item {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
  }

  .contact__item:not(:last-child) {
    margin-bottom: 20px;
  }

  .contact__label {
    white-space: nowrap;
    font-size: 20px;
    line-height: 30px;
    margin-right: 18px;
  }

  .contact__input {
    font-size: 20px;
    line-height: 30px;
    width: 100%;
    padding: 10px 20px;
  }

  .none {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .contact__list {
    display: flex;
    justify-content: space-between;
    padding-top: 75px;
    padding-bottom: 70px;
  }

  .contact__input {
    min-width: 486px;
  }

  .contact__item:not(:last-child) {
    margin-bottom: 0;
    padding: 76px;
  }
}
